import { fetchData } from './fetch';
const ENDPOINT_GET_PREFERENCES = '/neos/shel-neos-commandbar/preferences/getpreferences';
const ENDPOINT_SET_FAVOURITE_COMMANDS = '/neos/shel-neos-commandbar/preferences/setfavourites';
const ENDPOINT_ADD_RECENT_COMMAND = '/neos/shel-neos-commandbar/preferences/addrecentcommand';
async function setPreference(endpoint, data) {
    return fetchData(endpoint, data, 'POST');
}
export async function getPreferences() {
    return fetchData(ENDPOINT_GET_PREFERENCES);
}
export async function setFavouriteCommands(commandIds) {
    return setPreference(ENDPOINT_SET_FAVOURITE_COMMANDS, { commandIds: commandIds });
}
export async function addRecentCommand(commandId) {
    // TODO: Check if sendBeacon is a better option here to reduce the impact on the user
    return setPreference(ENDPOINT_ADD_RECENT_COMMAND, { commandId: commandId });
}
