const PREFIX = '%c[COMMANDBAR]';
const logger = {
    ok: (message, ...args) => console.log(PREFIX, 'color: green', message, ...args),
    error: (message, ...args) => console.log(PREFIX, 'color: red', message, ...args),
    warn: (message, ...args) => console.warn(PREFIX, 'color: orange', message, ...args),
    debug: 
    // @ts-ignore
    process.env.NODE_ENV === 'production'
        ? () => null
        : (message, ...args) => console.debug(PREFIX, 'color: lightblue', message, ...args),
};
export default logger;
